
import { defineComponent } from 'vue'
import { validateKey } from '../helpers/keyboard-helper'

export default defineComponent({
  created() {
    document.onkeydown = (e) => {
      const key = e.key.toUpperCase()
      this.pressKey(key)
    }
  },
  data() {
    return {}
  },
  methods: {
    pressKey(key: string) {
      if (!validateKey(key)) {
        return
      }

      this.$emit('letter', key)
    },
  },
  props: {
    keyRows: {
      type: Object,
      required: true,
    },
  },
})
