
import { defineComponent } from 'vue'
import { todaysWord } from '../helpers/word-chooser'

export default defineComponent({
  computed: {
    title(): string {
      return this.hasWon ? 'You won!' : 'You lost.'
    },
  },
  data() {
    return {
      todaysWord,
    }
  },
  methods: {
    close() {
      this.$emit('close')
    },
  },
  props: {
    hasWon: {
      type: Boolean,
      required: true,
    },
  },
})
